@import url(https://fonts.googleapis.com/css2?family=Ephesis&display=swap);
nav{background-color:#78909c}nav ul{display:flex;justify-content:flex-end;margin-right:3rem}nav ul li{padding:.5rem 1rem}nav ul li a{font-size:1.75rem;color:#fafafa}
footer{display:flex;justify-content:center;align-items:center;font-size:1.5rem;background-color:#4b636e;color:#fafafa}
.search{display:flex;justify-content:center;align-items:center;padding:3rem 0rem}.search input,.search button{font-size:2rem;padding:.25rem;border:2px solid #a7c0cd;border-radius:10px}.search button{margin-left:1rem;cursor:pointer;background-color:#4b636e;color:#fafafa}
.pictures{display:flex;flex-wrap:wrap;justify-content:center;align-items:center;min-height:80vh}.loadPicture{display:flex;justify-content:center;align-items:center;padding:2rem}.loadPicture button{padding:.25rem .5rem;font-size:2rem;border:none;color:#fafafa;background-color:#4b636e;border-radius:10px;cursor:pointer}
.picture{padding:1rem;width:400px;font-size:1.5rem}.picture .imgContainer{overflow:hidden;height:40vh}.picture .imgContainer img{width:100%;height:100%;object-fit:cover;transition:all .3s ease}.picture .imgContainer img:hover{-webkit-transform:scale(1.25);transform:scale(1.25)}
*{margin:0;padding:0;box-sizing:border-box;font-family:"Ephesis",cursive}a{text-decoration:none;color:#000}ul{list-style-type:none}
